.container{
  .jumbotron{
    //padding:5px 30px;
    background:transparent;
    @media (max-width:$medium) {
      padding:30px 15px;
    }
    &.missing-text{
      text-align:center;
      color:white;
    }
  }
  &.narrow{
    max-width:$small;
    .jumbotron{
      background:#eee;
      text-align:center;
      min-height:initial;
    }
  }
}
.slogan-highlight{
  font-weight:300;
  font-size: 18px;
  margin:10px auto;
  max-width:660px;
  padding:8px 15px;
  .glyphicon{
    font-size:32px;
    color:$twitch-purple;
    vertical-align:middle;
  }
}
.btn-twitch{
  background:$twitch-purple;
  color:#fff;
  transition:400ms all;
  border:0;
  &:hover,&:focus{
    background:$twitch-purple-hover;
    color:#fff;
  }
  .glyphicon{
    margin-right:10px;
  }
}
.approved-list{
  li{
    overflow:hidden;
    p{
      margin:0;
    }
  }
}
@import "stream";
@import "gallery";
@import "games";
@import "slogans";
.offline{
  text-align:center;
  color:$twitch-purple;
}
.loading,.stream_error{
  width:100%;
  margin:0 auto;
  padding:165px 0;
  //background:#fff;
  background:rgba(255,255,255,0.9);
  //height:380px;
  border-radius:2px;
  img{
    display:block;
    margin:0 auto;
    height:30px;
  }
  .text{
    color:$twitch-purple;
    display:block;
    font-weight:bold;
    text-align:center;
    &.error{
      color: #ca3b39;
    }
  }
}
#stream-loading{
  max-width:1200px;
  margin:30px auto;
}
.stream_error{
  .text{
    color: #ca3b39;
    font-size:1.3em
  }
  .btn{
    display:block;
    margin:0 auto;
    width:65%;
  }
}
.ad.horizontal{
  //width:728px;
  //width:992px;
  max-height:90px;
  //border:1px solid #ccc;
  margin:5px auto;
  padding:0;
  max-width:992px;
}
.ad.vertical{
  //height:500px;
  //width:100%;
  //border:1px solid #ccc;
}
.ad.block{
  margin:10px 0;
  text-align:center;
  max-height:300px;
  @media (max-width:$large) {
    margin:10px auto;
    padding:0;
  }
}
.with-ad{
  padding-left:15px;
}